import {
  ADD_NOTE,
  EDIT_NOTE,
  DELETE_NOTE,
  DELETE_NOTE_ALL,
  SET_ACTIVE_NOTE,
  TOGGLE_FAVORITE,
  TOGGLE_FILTER
} from '../constants'

import api from '../api'

// redux-thunkactionsdispatch
export const addNote = () => {
  return (dispatch, getState) => {
    dispatch({type: ADD_NOTE})
    // dispatchactionsstategetStateactions

    // ...
    setTimeout(() => {
      console.log('stateshow: ', getState().show)
      api.getAccount()
        .then(res => {
          dispatch(editNote(`: ${res.name}`))
        })
        .catch(err => {
          console.log(err)
        })
    }, 2000)
  }
}

// redux-thunk
// export const addNote = () => ({ type: ADD_NOTE })

export const editNote = text => ({
  type: EDIT_NOTE,
  text
})

export const deleteNote = index => ({ 
  type: DELETE_NOTE,
  index
})

export const deleteNoteAll = () => ({ type: DELETE_NOTE_ALL })

export const setActiveNote = (note) => ({
  type: SET_ACTIVE_NOTE,
  note
})

export const toggleFavorite = index => ({ 
  type: TOGGLE_FAVORITE,
  index
})

/* return */
export const toggleFilter = style => ({
  type: TOGGLE_FILTER,
  style
})



